.signup-box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60%;
    max-width: 900px;
    min-width: 865px;
    height: 70%;
    max-height: 600px;
    min-height: 410px;
    padding: 0px;
    transform: translate(-50%, -50%);
    background: rgba(255, 255, 255, 0.3);
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(0, 0, 0, .6);
    border-radius: 10px;
}

.signup-box .input-area-signup {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: #dfebe1;
    background-size: cover;
    border-radius: 0px 10px 10px 0px;
    padding: 30px;
}

.input-area-signup .title {
    font-weight: bold;
    margin: 0 0 60px;
    padding: 0;
    color: #2a2a2a;
    text-align: center;
}

.signup-box .user-box {
    position: relative;
}

.signup-box .user-box input {
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: #2a2a2a;
    margin-bottom: 20px;
    border: none;
    border-bottom: 1px solid #2a2a2a;
    border-radius: 0;
    outline: none;
    background: transparent;
}

.signup-box .user-box input:focus {
    outline: none;
}

.signup-box .user-box label {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px 0;
    font-size: 16px;
    color: #2a2a2a;
    pointer-events: none;
    transition: .5s;
}

.signup-box .user-box input:focus~label,
.signup-box .user-box input:valid~label {
    top: -20px;
    left: 0;
    color: #2a2a2a;
    font-size: 12px;
}

.signup-box form {
    width: 75%;
}

.signup-box form a {
    position: relative;
    display: inline-block;
    padding: 10px 20px;
    color: #2a2a2a;
    font-size: 19px;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    transition: .3s;
    margin-top: 20px;
    letter-spacing: 2px;
}

.signup-box a:hover {
    font-size: 22px;
    font-weight: bold;
    color: #2a2a2a;
    cursor: pointer;
}

.signup-btn {
    text-align: center;
}

.signup-btn-signup {
    color: aquamarine;
}