.layout {
  min-height: 100vh;
}

.primary-color {
  color: #9ec1a3;
}

.text-bold {
  font-weight: bold;
}

.align-center {
  text-align: center;
}

.align-items-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 200px;
  height: 63px;
  text-align: center;
  vertical-align: bottom;
  pointer-events: none;
  color: white;
}

.logo-img {
  width: 200px;
  height: 63px;
}


/* header */

.header {
  padding: 0;
  background-color: rgb(240, 240, 240);
  font-size: 5vh;
  text-align: center;
  vertical-align: top;
  z-index: 1;
  top: 0;
  position: sticky;
}

.nav-alignRight {
  text-align: center;
}

.title {
  padding-left: 20px;
  text-align: left;
  font-size: 22px;
  font-weight: bold;
  color: #88aa8d;
}

.title a {
  color: #88aa8d;
}

.title a:hover {
  color: #b0dab7;
}

.menu {
  justify-content: center;
  background-color: rgb(240, 240, 240);
  font-size: medium;
}

.menu li {
  /* margin: 0 20px 0 20px; */
}

.menu li:active {
  color: #b0dab7;
}


/* content */

.content {
  height: 100%;
  background-color: #FFFFFF;
}

.contentBox {
  padding: 30px;
  min-height: 360;
  background: #FFFFFF;
}

.breadcrumb {
  margin: 16px 0;
  font-weight: bold;
}

.searchBox {
  border: 2px solid #cccccc;
  border-radius: 5px;
  padding: 10px 10px 10px 10px;
  font-weight: bold;
}

.searchBox-row {
  align-items: center;
  text-align: center;
}

.searchBox-col {
  padding: 5px 0;
}

.searchBox-col-alignLeft {
  padding: 5px 0;
  text-align: left;
}

.searchBox-col-alignRight {
  padding: 5px 0;
  text-align: right;
}

.searchBox-selector {
  width: '100%';
}

.row {
  min-height: 1vh;
}

.col-title {
  font-size: 2em;
  font-weight: bold;
  margin: 50px 0px 10px;
}

.col-content {
  word-spacing: 2px;
  line-height: 2em;
}

.col-content p {
  margin-top: 0;
  margin-bottom: 5px;
}

.timeline {
  margin-top: 20px;
  line-height: 22px;
  font-weight: bold;
  font-size: 50px;
}


/* footer */

.footer {
  text-align: center;
}


/* form */

.form-item {
  margin-bottom: 35px;
}

.form-label {
  margin-left: 5px;
}

.form-input {
  line-height: 30px;
  margin-top: 8px;
}

.form-input:hover {
  border-color: #7e9882;

}

.form-input:focus {
  border-color: #7e9882;
  box-shadow: 0 0 3px 0px #7e9882;
}


/* h1 */

.content-title {
  font-size: 32px;
}


/* button */

.btn-fade {
  display: block;
  position: relative;
  float: left;
  padding: 0;
  font-weight: 600;
  font-size: 15px;
  text-align: center;
  line-height: 40px;
  border: none;
  border-radius: 5px;
  transition: all 0.3s;
  cursor: pointer;
  width: 120px;
}

.btn-full {
  width: 100%;
  margin: 4px 0 4px;
}

.btn-sm {
  width: 80px;
  margin: 4px;
}

.btn-lg {
  width: 160px;
  margin: 4px;
}

.btn-primary {
  background-color: #9ec1a3;
  color: #fff;
}

.btn-primary:hover {
  background-color: #7e9882;
  color: #fff;
}


/* a */

.link {
  display: block;
  position: relative;
  float: left;
  padding: 0;
  font-weight: 600;
  font-size: 15px;
  text-align: center;
  line-height: 40px;
  border: none;
  border-radius: 5px;
  transition: all 0.3s;
  cursor: pointer;
}

.link-full {
  width: 100%;
  margin: 4px 0 4px;
}

.link-primary {
  color: #9ec1a3;
}

.link-primary:hover {
  color: #7e9882;
}


/* table */

.table {
  margin-top: 30px;
}

